
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
}
button {
  font-family: Arial;
  color: #ffffff;
  font-size: 18px;
  background: #3498db;
  padding: 6px;
  border: solid #1e80bd 2px;
  text-decoration: none;
}
button:hover {
  background: #3cb0fd;
  text-decoration: none;
}



